@-webkit-keyframes loader {
  0% {
    opacity: 100%;
    transform: translateY(0);
  }
  50% {
    opacity: 40%;
    transform: translateY(-50%);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

@keyframes loader {
  0% {
    opacity: 100%;
    transform: translateY(0);
  }
  50% {
    opacity: 40%;
    transform: translateY(-50%);
  }
  100% {
    opacity: 100%;
    transform: translateY(0);
  }
}

@-webkit-keyframes rotate {
  100% {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

@keyframes rotate {
  100% {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

@keyframes waves {
  0% {
    transform: translateX(-100%)
  }
  60% {
    transform: translateX(100%)
  }
  100% {
    transform: translateX(100%)
  }
}